@import "variables";
@import "base";
@import "attributes";

/* util classes */
.link-button {
  padding: 0.5em;
  color: var(--colour-anchor-primary);
  background-color: var(--colour-anchor-secondary);
  border-radius: 5px;
  border-bottom: .1em solid transparent;

  &:hover {
    border-color: var(--colour-anchor-primary);
  }
}
/* END util classes */

.gheader {
  position: sticky;
  top: -1px;
  z-index: 1;
  width: 100%;
  background-color: var(--colour0-quarternary);
  
  &.stuck {
    box-shadow: 0.125em 0.125em 0.125em 0.125em hsla(0, 0%, 0%, 0.5);

    & .gtop,
    & .gnav,
    & .gforms {
      visibility: hidden;
      opacity: 0;
      max-height: 0;
    }

    & .gforms-switch {
      visibility: visible;
      opacity: 1;
      max-height: initial;
      padding-top: 1em;
    }
  }

  & form {
    padding: 0;
    border: none;
  }

  & input,
  & button {
    margin: 0;
  }

  & .gtop {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
  }

  & .glogo {
    font-size: 1.5em;
  }

  & .gnav {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: center;
    gap: 1em;

    & .navlink {
      padding: 1em;
    }

    & .navanchour {
      font-size: 1.3em;
    }

    @media (max-width: calc(50em - 1px)){
      display: none;
    }
  }

  & .gforms {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1em;

    &.shown {
      visibility: visible;
      opacity: 1;
      max-height: initial;
    }
  }

  & .gauth {
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    align-items: center;
    gap: 1em;
    padding: 0.5em;
  }

  & .gauth-login {
    padding: 0;
  }

  & .login-switcher {
    border: 1px solid var(--colour0-primary);
    border-radius: 5px;
  }

  & .login-switch {
    display: none;

    & + .gauth-form {
      position: absolute;
      top: calc(100% + 1px);
      left: 0;
      opacity: 0;
      visibility: hidden;
      z-index: -1;
    }

    &:checked + .gauth-form {
      opacity: 1;
      visibility: visible;
      z-index: 1;
    }
  }

  & .gauth-form {
    display: flex;
    flex-flow: column nowrap;
    align-items: stretch;
    gap: 1em;
    width: calc(100vw - 2em);
    max-width: $width-mobile;
    background-color: var(--colour0-quarternary);
    padding: 1em;

    &-fields {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;
    }
  }

  & .gsearch {
    display: flex;
    flex-flow: row nowrap;
    justify-content: start;
    align-items: center;

    & .search-pane {
      position: relative;

      & .search-options {
        position: absolute;
        top: 100%;
        visibility: hidden;
        z-index: -1;
        opacity: 0;
        background-color: var(--colour-anchor-local-secondary);
        padding: 1em;
      }

      & .search-field {
        margin: 0;
      }

      &:focus-within .search-options {
        visibility: visible;
        z-index: 1;
        opacity: 1;
      }
    }
  }

  & .gforms-switch {
    visibility: hidden;
    opacity: 0;
    max-height: 0;
    text-align: center;

    & > button {
      width: auto;
    }
  }

  & .gburger {
    display: none;
    position: fixed;
    bottom: 2em;
    right: 2em;
    z-index: 1;

    &-button {
      background-color: var(--colour0-secondary);
      border-radius: 50%;
      border: 1px solid var(--colour0-primary);
      padding: 1em;
    }

    &-switch {
      display: none;

      & + .gnav-burger {
        opacity: 0;
        visibility: hidden;
      }

      &:checked + .gnav-burger {
        opacity: 1;
        visibility: visible;
      }
    }

    & .gnav-burger {
      position: fixed;
      bottom: 1em;
      right: 50%;
      border: 2px solid var(--colour0-primary);
      background-color: var(--colour0-secondary);
      padding: 1em;
      transform: translate(50%);

      & .close-button {
        padding: 0.5em;
        float: right;
        text-align: right;
        min-width: 44px;
        min-height: 44px;

        & label {
          border-radius: 50%;
          padding: 0.5em;
          color: var(--colour-negative-secondary);
          background-color: var(--colour-negative-primary);
        }
        
      }

      & .navlink {
        clear: right;
        padding: 0.5em 0;
      }
    }

    @media (max-width: 50em){
      display: block;
    }
  }
}

section.lorems {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: stretch;
  gap: 1em;
  padding: 1em;

  & .lorem-article {
    flex: 0 1 20em;
    display: flex;
    flex-flow: column nowrap;
    opacity: 0.8;
    color: var(--colour0-tertiary);
    background-color: var(--colour0-quarternary);
    border-radius: 10px;
    border: 2px solid var(--colour0-primary);
    transition-property: transform, opacity;
    
    &:hover {
      opacity: 1;
      transform: scale(1.05);
    }

    & > * {
      padding: 1em;
      flex: 0 0 auto;
    }

    & > header {
      border-bottom: 1px solid var(--colour0-secondary);
    }

    & > p {
      flex: 1 1 auto;
    }

    & > footer {
      border-top: 1px solid var(--colour0-secondary);
    }
  }
}